import { Component } from '@angular/core';
import { NavParams, AlertController, ViewController } from 'ionic-angular';
import { AppService, AppConfig } from '../../app/app.service';
@Component({
  selector: 'gift-info',
  templateUrl: 'gift-info.html'
})
export class GiftInfo {
  isAllow: boolean = true;
  giftInfo: any = {
    "memberGiftAccountSeq": null,
    "giftSeq": null,
    "giftCode": "",
    "giftName": "",
    "giftType": "",
    "imageName": "",   //
    "giftRemark": "",
    "brandshopSeq": null,
    "brandshopName": "",    //
    "startDate": null,
    "endDate": null,
    "status": "",
    "receiveDate": null,         //
    "useDate": null,
    "memberSeq": null,
    "memberPhone": null,
    "reservePhone": "",
    "reserveShopTime": null,
    "expressCompany": null,
    "expressNo": null,
    "deliveryTime": null,
    "brandshopUserSeq": null,
    "brandshopUserName": null,
    "attrValueList": null
  };
  constructor(
    public alertCtrl: AlertController,
    public viewCtrl: ViewController,
    public navParams: NavParams,
    public appService: AppService
  ) {
  }
  ionViewDidEnter() {
    this.getGiftDetail();
  }
  presentConfirm() {
    if (!this.isAllow) {
      return;
    }
    this.isAllow = false;
    let url = `${AppConfig.API.receiveGift}`;
    let body = {
      giftCode: this.giftInfo.giftCode
    }
    this.appService.httpPost(url, body)
      .then(data => {
        this.isAllow = true;
        this.alertLayer();
      })
      .catch(error => {
        this.isAllow = true;
        console.log(error);
      });
  }
  alertLayer() {
    const alert = this.alertCtrl.create({
      message: '',
      enableBackdropDismiss: false,
      buttons: [
        {
          text: '',
          handler: () => {
            let data = { 'type': '0' };
            //
            this.viewCtrl.dismiss(data);
          }
        },
        {
          text: '',
          handler: () => {
            let data = { 'type': '1' };
            this.viewCtrl.dismiss(data);
          }
        }
      ]
    });
    alert.present();
  }
  getGiftDetail() {
    let url = this.navParams.get("url"); //;
    this.appService.httpGet(url)
      .then(data => {
        console.log(data);
        this.giftInfo = data;
      }).catch(error => {
        console.log(error);
        this.appService.getToken(error, () => {
          this.getGiftDetail();
        });
        if (error.type) {
          const alert = this.alertCtrl.create({
            message: error.message,
            enableBackdropDismiss: false,
            buttons: [
              {
                text: '',
                handler: () => {
                  this.viewCtrl.dismiss();
                }
              }
            ]
          });
          alert.present();
        }else {
          this.appService.toast("", 1000, "middle");
        }  
      });
  }
}



