import { Injectable, EventEmitter } from '@angular/core';
import { LoadingController, Loading, ToastController } from 'ionic-angular';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/timeout';
import { Buffer } from 'buffer';
import { ENV } from '@app/env'

@Injectable()
export class AppConfig {

  //
  static mainUrl: string = ENV.mode;
  // static hostUrl: string = `https://rest.${AppConfig.mainUrl}`;
  static hostUrl: string = `http://192.168.31.203:8082`;
  static imgUrl: string = `https://images.${AppConfig.mainUrl}/`;
  //
  static TIME_OUT: number = 30000;
  // 
  static LOAD_TIME: number = 500;
  // token1800000
  static RESERVED_TIME: number = 1800000;
  //tokenurl
  static oauthTokenUrl: string = `${AppConfig.hostUrl}/uaa/oauth/token`;
  static client_id: string = ENV.client_id;
  static secret: string = ENV.secret;
  static grant_type: string = "password";
  //url
  static API: any = {
    login: `${AppConfig.hostUrl}/uaa/user`,
    getOrderList: `${AppConfig.hostUrl}/order/bssList`,    ///
    getCancelorder: `${AppConfig.hostUrl}/order/cancel/list`,    ///
    auditCancelOrder: `${AppConfig.hostUrl}/order/cancel/approval`,    //
    getReturnorderList: `${AppConfig.hostUrl}/order/return/list`,    ///
    returnDetail: `${AppConfig.hostUrl}/order/return/details`,    //
    returnReceived: `${AppConfig.hostUrl}/order/return/received`,    // -
    auditReturnOrder: `${AppConfig.hostUrl}/order/return/approval`,    //
    getGiftList: `${AppConfig.hostUrl}/promotion/member/gift/account/getGiftList`,//
    getUnhandleGiftCount: `${AppConfig.hostUrl}/promotion/member/gift/account/getUnhandleGiftCount`,//
    confirmReserveShopTime: `${AppConfig.hostUrl}/promotion/member/gift/account/confirmReserveShopTime`,//
    confirmExpressInfo: `${AppConfig.hostUrl}/promotion/member/gift/account/confirmExpressInfo`,//
    getBrandshopProducts: `${AppConfig.hostUrl}/product/getBrandshopProducts`,//
    warehouseGetCount: `${AppConfig.hostUrl}/order/warehouse/getCount`,//
    getProductSkuWithDefault: `${AppConfig.hostUrl}/product/sku/getProductSkuWithDefault`,//SKU
    getValidSKUAttrValue: `${AppConfig.hostUrl}/product/sku/getValidSkuAttrValue`,//SKU
    warehouseAdd: `${AppConfig.hostUrl}/order/warehouse/add`,//
    warehouseList: `${AppConfig.hostUrl}/order/warehouse/list`,//
    warehouseGenerateCode: `${AppConfig.hostUrl}/order/warehouse/generateCode`,//
    warehouseDeleteById: `${AppConfig.hostUrl}/order/warehouse/item/deleteById`,//
    warehouseUpdate: `${AppConfig.hostUrl}/order/warehouse/item/update`,//
    warehouseEmpty: `${AppConfig.hostUrl}/order/warehouse/empty`,//
    checkStatus: `${AppConfig.hostUrl}/order/warehouse/checkStatus`,//
    current: `${AppConfig.hostUrl}/account/brandshop/user/current`, //
    account: `${AppConfig.hostUrl}/account/brandshop/user/account`, //
    withdraw: `${AppConfig.hostUrl}/account/brandshop/user/withdraw/`, //
    qrcode: `${AppConfig.hostUrl}/account/brandshop/user/qrcode`, //
    withdrawList: `${AppConfig.hostUrl}/account/brandshop/user/withdraw/list`, //
    bonusList: `${AppConfig.hostUrl}/account/brandshop/user/bonus/list`, //
    bonusSum: `${AppConfig.hostUrl}/account/brandshop/user/bonus/sum`,
    untreatedCount: `${AppConfig.hostUrl}/order/untreatedCount`,//
    orderReceive: `${AppConfig.hostUrl}/order/receive/received`, //
    receiveGift: `${AppConfig.hostUrl}/promotion/member/gift/account/receiveGift`,
    firstLogin: `${AppConfig.hostUrl}/uaa/getInfo`,//
    editPassword: `${AppConfig.hostUrl}/uaa/password`,//
    withdrawTax: `${AppConfig.hostUrl}/account/brandshop/user/withdraw/tax`,//
  };
  // ion-spinner
  static load: any = {
    spinner: 'dots',
    content: ''
  }
}
@Injectable()
export class AppService {
  withTokenHeaders: any;
  oauthTokenHeaders: any;
  constructor(
    private http: Http,
    public loadingCtrl: LoadingController,
    private toastCtrl: ToastController
  ) {
  }
  public event: EventEmitter<any> = new EventEmitter<any>();
  public eventBrand: EventEmitter<any> = new EventEmitter<any>();

  //get request with Authorization
  httpGet(url: string) {
    this.withTokenHeaders = new Headers({
      'Authorization': 'Bearer ' + this.getItem('tpb_token')
    });
    return this.http.get(url, { headers: this.withTokenHeaders }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //get request with No Authorization
  httpGetNoAuthor(url: string) {
    return this.http.get(url).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //get request
  httpGetReturnData(url: string) {
    this.withTokenHeaders = new Headers({
      'Authorization': 'Bearer ' + this.getItem('tpb_token')
    });
    return this.http.get(url, { headers: this.withTokenHeaders }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res)
      .catch(this.handleError);
  }

  //get request with headers
  httpGetHeader(url: string, header: any) {
    return this.http.get(url, { headers: header }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //post request
  httpPost(url: string, body: any) {
    this.withTokenHeaders = new Headers({
      'Authorization': 'Bearer ' + this.getItem('tpb_token'),
      'content-type': 'application/json'
    });
    return this.http.post(url, body, { headers: this.withTokenHeaders }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //post headers 
  httpPostHeader(url: string, body: any, header: any) {
    return this.http.post(url, body, { headers: header }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json());
  }

  //put request
  httpPut(url: string, parameters: any) {
    this.withTokenHeaders = new Headers({
      'Authorization': 'Bearer ' + this.getItem('tpb_token')
    });
    return this.http.put(url, parameters, { headers: this.withTokenHeaders }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //delete request
  httpDelete(url: string) {
    this.withTokenHeaders = new Headers({
      'Authorization': 'Bearer ' + this.getItem('tpb_token')
    });
    return this.http.delete(url, { headers: this.withTokenHeaders }).timeout(AppConfig.TIME_OUT).toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }

  //access_token
  private handleError(error: any) {
    return Promise.reject(error.json() || error);
  }

  getToken(error, callback) {
    let self = this;
    if (error.error == "invalid_token") {
      let base64encode = new Buffer(`${AppConfig.client_id}:${AppConfig.secret}`).toString('base64');
      self.oauthTokenHeaders = new Headers({
        'Authorization': 'Basic ' + base64encode,
        'Content-Type': 'application/x-www-form-urlencoded'
      });
      let oauthTokenUrl = AppConfig.oauthTokenUrl;
      let body = `grant_type=refresh_token&refresh_token=${self.getItem("refresh_token")}`;
      self.httpPostHeader(oauthTokenUrl, body, self.oauthTokenHeaders).then(data => {
        let newDateMS = (new Date()).getTime() + data.expires_in * 1000 - AppConfig.RESERVED_TIME;
        self.setItem("newDateMS", newDateMS);
        self.setItem("tpb_token", data.access_token);
        self.setItem("refresh_token", data.refresh_token);
        callback();
      }).catch(err => {
        console.log(err);
        self.toast('', 1000, 'middle');
        self.setItem("tpb_token", "");
        self.setItem("refresh_token", "");
        setTimeout(history.go(0), 1000);
      })
    }
  }

  //loader.present();
  public loading(): Loading {
    let loader = this.loadingCtrl.create({
      spinner: "dots",
      content: "",
      dismissOnPageChange: true, //loading
      showBackdrop: false //
    });
    return loader;
  }

  // 
  toast(message: string, duration: number, position: string) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: duration,
      position: position
    });
    toast.present();
  }

  //localstoragekey
  setItem(key: string, value: any) {
    try {
      window.localStorage[key] = value;
    }
    catch (e) {
      console.error("window.localStorage error:" + e);
    }
  }

  //localstoragekey
  getItem(key: string) {
    try {
      return window.localStorage[key];
    }
    catch (e) {
      console.error("window.localStorage error:" + e);
    }
  }

  // yyyy-mm-dd
  reserveDate() {
    let fillZero = (n) => {
      let result = (n).toString().length === 1 ? ('0' + n) : n;
      return result;
    }
    let formatTime = () => {
      let d = new Date();
      let year = d.getFullYear();
      let month = d.getMonth() + 1;
      let date = d.getDate();
      let result = `${year}-${fillZero(month)}-${fillZero(date)}`;
      return result;
    }
    let res = formatTime();
    return res;
  }
}



