import { PipeTransform, Pipe } from '@angular/core';
import { AppConfig } from './app.service';

// 
// pass:  audit (css)
@Pipe({ name: 'setOrderStatus' })
export class FilterStatusPipe implements PipeTransform {
  transform(param: string): any {
    switch (param) {
      case "0":
        return {
          status: "",
          pass: false,
          audit: true
        };
      case "1":
        return {
          status: "",
          pass: true,
          audit: false
        };
      case "2":
        return {
          status: "",
          pass: true,
          audit: false
        };
      case "3":
        return {
          status: "",
          pass: true,
          audit: false
        };
      case "4":
        return {
          status: "",
          pass: false,
          audit: true
        };
      case "6":
        return {
          status: "",
          pass: true,
          audit: false
        };
      case "C":
        return {
          status: "",
          pass: true,
          audit: false
        }
    }
  }
}

