import { Component, ViewChild, NgZone } from '@angular/core';
import { NavParams, AlertController, Content, ModalController, Events } from 'ionic-angular';
import { AppService, AppConfig } from '../../app/app.service';
import { UnhandleSelfPage } from '../unhandle-self/unhandle-self';
import { UnhandleExpressPage } from '../unhandle-express/unhandle-express';
import { SuperTabs } from 'ionic2-super-tabs';
@Component({
  selector: 'unhandle-tabs',
  templateUrl: 'unhandle-tabs.html'
})
export class UnhandleTabs {
  @ViewChild(Content) content: Content;
  @ViewChild(SuperTabs) superTabs: SuperTabs;
  self: any = UnhandleSelfPage;
  express: any = UnhandleExpressPage;
  selfGiftCount: string = '';
  expressGiftCount: string = '';
  limit: number = 10;
  start: number = 0;
  constructor(
    public alertCtrl: AlertController,
    public navParams: NavParams,
    public appService: AppService,
    public modalCtrl: ModalController,
    public zone: NgZone,
    public events: Events

  ) {
    this.start = 0;
    // tab
    events.subscribe('express:created', (data, time) => {
      UnhandleTabs.prototype.expressGiftCount = `${data}`; 
      this.expressGiftCount = UnhandleTabs.prototype.expressGiftCount;
    });
    events.subscribe('self:created', (data, time) => {
      UnhandleTabs.prototype.selfGiftCount = `${data}`; 
      this.selfGiftCount = UnhandleTabs.prototype.selfGiftCount;
    });

    this.getTabCount();
  }
  // tab
  getTabCount() {
    let urlExpress = `${AppConfig.API.getGiftList}?type=1&start=${this.start}&limit=${this.limit}`;
    let urlSelf = `${AppConfig.API.getGiftList}?type=0&start=${this.start}&limit=${this.limit}`;
    this.appService.httpGet(urlExpress).then(data => {
      this.expressGiftCount =`${data.count}`;
    }).catch(error => {
      this.appService.getToken(error, () => {
        this.getTabCount();
      });
      if (error.error != "invalid_token") {
        this.appService.toast('', 1000, 'middle');
      }
    });
    this.appService.httpGet(urlSelf).then(data => {
      this.selfGiftCount =`${data.count}`;
    }).catch(error => {
      this.appService.getToken(error, () => {
        this.getTabCount();
      });
      if (error.error != "invalid_token") {
        this.appService.toast('', 1000, 'middle');
      }
    })
  }
  
}


