import { Component } from '@angular/core';
import { NavParams, AlertController, ViewController } from 'ionic-angular';
import { AppService, AppConfig } from '../../app/app.service';
@Component({
  selector: 'order-info',
  templateUrl: 'order-info.html'
})
export class OrderInfo {
  isAllow: boolean = true;
  orderDetail: any = {
    "orderSeq": null,//Seq
    "orderId": "",//ID
    "brandshopName": "",//
    "orderItemProductSkuDTOS": [//SKU
      {
        "orderItemSeq": null,//Seq
        "prodSeq": null,//Seq
        "skuSeq": null,//SkuSeq
        "unitPrice": 0,//
        "number": 0,//
        "productSkuDTO": {
          "productSeq": null,//Seq
          "skuSeq": null,//SKUseq
          "productName": "",//
          "fileSeq": null,//ID
          "attrValueList": [//SKU
            {
              "skuSeq": null,
              "attrSeq": null,
              "attrName": "",
              "attrValue": "",
              "type": null,
              "fileSeq": null,
              "price": null,
              "selectedAttrValue": null,
              "invalidAttrValue": null
            }
          ],
          "fallback": null
        }
      }
    ]
  };
  constructor(
    public alertCtrl: AlertController,
    public viewCtrl: ViewController,
    public navParams: NavParams,
    public appService: AppService
  ) {
  }
  ionViewDidEnter() {
    this.getOrderDetail();
  }
  presentConfirm() {
    if (!this.isAllow) {
      return;
    }
    this.isAllow = false;
    let url = `${AppConfig.API.orderReceive}`;
    this.appService.httpPost(url, this.orderDetail.orderSeq)
      .then(data => {
        this.isAllow = true;
        this.alertLayer();
      })
      .catch(error => {
        this.isAllow = true;
        console.log(error);
      });
  }
  alertLayer() {
    const alert = this.alertCtrl.create({
      message: '',
      enableBackdropDismiss: false,
      buttons: [
        {
          text: '',  //type: 0
          handler: () => {
            let data = { 'type': '0' };
            this.viewCtrl.dismiss(data);
          }
        },
        {
          text: '',  //type: 1
          handler: () => {
            let data = { 'type': '1' };
            this.viewCtrl.dismiss(data);
          }
        }
      ]
    });
    alert.present();
  }
  getOrderDetail() {
    let url = this.navParams.get("url"); //;
    this.appService.httpGet(url)
      .then(data => {
        console.log(data);
        this.orderDetail = data;
      }).catch(error => {
        console.log(error);
        this.appService.getToken(error, () => {
          this.getOrderDetail();
        });
        if (error.type) {
          const alert = this.alertCtrl.create({
            message: error.message,
            enableBackdropDismiss: false,
            buttons: [
              {
                text: '',
                handler: () => {
                  this.viewCtrl.dismiss();
                }
              }
            ]
          });
          alert.present();
        }else {
          this.appService.toast("", 1000, "middle");
        }
      });
  }
}



